  import React from 'react';
import { storiesOf } from '@storybook/react';
import { withInfo } from '@storybook/addon-info';
import { withKnobs, text } from '@storybook/addon-knobs';
import WizardStepSummary from '@automatic-labs/ui-components/wizard/wizard-step-summary';

WizardStepSummary.displayName = 'WizardStepSummary';

const summaryDetails = [
  { title: 'First Name', value: 'John' },
  { title: 'Last Name', value: 'Doe' },
  { title: 'Email', value: 'john.doe@email.com' }
];

storiesOf('Wizard (deprecated) > WizardStepSummary', module).addDecorator(withStorySource(__STORY__, __ADDS_MAP__))
  .addDecorator(withKnobs)
  .add('Default',
    withInfo(`
      Wizard Step Summary is a component to display a summary of information from the previous steps and going to the next step.
    `)(() => (
      <WizardStepSummary
        summary={summaryDetails}
        advanceButtonLabel={text('Button label', 'Done')}
        headerTitle={text('Title', 'Sample text added to title')}
        headerCaption={text('Caption', 'Sample caption')}
        headerPreTitle={text('PreTitle', 'Sample text')}
      />
    ))
  );

  